import React, {Component} from 'react';
import Article from './Article';

class Section extends Component{
  constructor(props){
    super(props);
    this.state = {};

    this.loadMore = this.loadMore.bind(this);
  }

  componentWillMount(){
    if (this.props.name==='Popular'||'Trending'){
      this.initColumns(this.props.articles);
    }
  }

  initColumns(articles){
    this.col = [];
    this.remainder = [];
    articles.forEach((article, i)=>{
      if (i > 9){
        this.remainder.push(article);
      } else {
        this.col.push(article);
      }
    });
  }

  loadMore(e){
    if (this.remainder.length < 5){
      this.col.concat(this.remainder);
    } else {
      // Copy the articles I want to load
      let loadedArticles = this.remainder.slice(0,5);
      //Remove the articles loaded from remainder array
      this.remainder.splice(0,5)
      this.col = this.col.concat(loadedArticles);
      // Rerender
      this.setState({});
    }
  }

  render(){
    const {name, articles} = this.props;
    if (!articles){
      return null;
    } 
    return(
      <div className={`${name}`}>
        <h3 className={`${name}_heading`}>{name}</h3>
        <div className='col2'>
          {this.col.map((article,i)=>{
            return <Article key={i} articleObj={article}/>
          })}
        </div>
        <br/>
        {(this.remainder.length > 0) ? (<LoadMore load={this.loadMore}/>) : null}
      </div>
    )
  }
}

const LoadMore = ({load}) =>{
  return (
    <button
      className='btn'
      onClick={(e)=>{
        e.preventDefault();
        load()}}>
      Load More!
    </button>)
  }

export default Section;


