import React, { Component } from 'react';
import Section from '../Components/Section';

class Page extends Component {
  constructor(props){
    super(props);
    this.state = {
      topArticles: null,
      recentArticles: null
    }
  }

  componentWillMount(){
    console.log('mounted')
    let topicPath = this.props.path;
    if (topicPath.length < 2){
      this.fetchArticles('');
    }
    this.fetchArticles(topicPath.substring(1));
  }

  componentWillReceiveProps(nextProps){
    if ((nextProps.minutesUntilUpdate===0)&&
    (nextProps.secondsUntilUpdate===0)){
      let topicPath = nextProps.path;
      if (topicPath.length < 2){
        this.fetchArticles('');
      }
      this.fetchArticles(topicPath.substring(1));
    }
    if (nextProps.path!==this.props.path){

    }
  }

  fetchArticles(topic){
    let top_url = `https://newsapi.org/v2/top-headlines?q=${topic}&sources=the-wall-street-journal&apiKey=e3aa56ecd2054df892a9eedd4eca5fb0`;
    let recent_url = `https://newsapi.org/v2/everything?q=${topic}&sources=the-wall-street-journal&apiKey=e3aa56ecd2054df892a9eedd4eca5fb0`;
    let topArticles = [];
    let recentArticles = [];

    fetch(top_url, {method: 'GET'}).then(data=>{
      return data.json();
    }).then(data=>{
      data.articles.forEach((article)=>{
        topArticles.push(article);
      });
      console.log(topArticles);
      this.setState({topArticles: topArticles});
    });

    fetch(recent_url, {method: 'GET'}).then(data=>{
      return data.json();
    }).then(data=>{
      data.articles.forEach((article)=>{
        recentArticles.push(article);
      });
      this.setState({recentArticles: recentArticles});
    });
  }

  render() {
    const {topArticles, recentArticles} = this.state;
    let top, recent;
    // console.log(topArticles);
    if (!topArticles){
      top = 'Loading...';
    } else {
      top = (<Section name='Popular' articles={topArticles}/>);
    } 
    if (!recentArticles){
      recent = 'Loading...';
    } else {
      recent = (<Section name='Trending' articles={recentArticles}/>);
    }

    return (
      <div>
        <br/>
        <div className="home">
          <div className='row'>
            <div className='col-md-6'>
              {top}
            </div>
            <div className='col-md-6'>
              {recent}
            </div>
          </div>
          <div className=''>
          </div>
        </div>
      </div>
    );
  }
}

export default Page;


