import React, {Component} from 'react';
import {
  Route,
  NavLink,
} from 'react-router-dom';
import SearchIcon from '../Images/SearchIcon';
import Page from '../Pages/Page';

let minutes = 5;
let seconds = 59;

class TopicsNavBar extends Component{
  state = {
    date: new Date(),
    minutesUntilUpdate: minutes,
    secondsUntilUpdate: seconds,
  }

  componentWillMount(){
    this.pageRoutes = this.props.tabs.map((tab,i) =>
      (tab.path==='/') ? <Route key={i} path={tab.path} render={(props)=><Page exact path={tab.path}minutesUntilUpdate={this.state.minutesUntilUpdate}secondsUntilUpdate={this.state.secondsUntilUpdate}/>}/>
      :<Route key={i} path={tab.path} render={(props)=><Page path={tab.path}minutesUntilUpdate={this.state.minutesUntilUpdate}secondsUntilUpdate={this.state.secondsUntilUpdate}/>}/>
    );
      
    this.links = this.props.tabs.map(({name,path},i)=> 
      (path==='/') ? <li key={i}><NavLink className='nav-link' exact to='/'>Home</NavLink></li>
      :<li key={i}><NavLink className='nav-link' to={path}>{`${name}`}</NavLink></li>
    );
  }

  componentDidMount(){
    this.timer = setInterval(()=> this.tick(), 1000);
  }

  componentWillUnmount(){
    clearInterval(this.timer);
  }

  tick(){
    this.setState({
      date: new Date()
    });

    if (this.state.secondsUntilUpdate <= 0){
      this.setState((prevState)=>({
        minutesUntilUpdate: prevState.minutesUntilUpdate - 1,
        secondsUntilUpdate: seconds
      }));
    }
    if (this.state.minutesUntilUpdate < 0){
      this.setState({
        minutesUntilUpdate: minutes,
        secondsUntilUpdate: seconds
      })
    } else {
      this.setState((prevState)=>({
        minutesUntilUpdate: prevState.minutesUntilUpdate,
        secondsUntilUpdate: prevState.secondsUntilUpdate - 1
      }))
    }
  }

  render(){
    const {type} = this.props;
    const {date,minutesUntilUpdate,secondsUntilUpdate} = this.state;

    return(
      <div className={type}>
        <div className='wrapper'>
          <ul className='nav justify-content-center'>
            {this.links}
          </ul>
          <div className='fetch_timer'>
            The time is now {date.toLocaleTimeString()}.
            Articles will update in {minutesUntilUpdate} minutes and {secondsUntilUpdate} seconds.
          </div>
          {this.pageRoutes}
        </div>
        <SearchIcon/>
      </div>
    );
  }
}

export default TopicsNavBar;


