import React, {Component} from 'react';
import ArticleModal from './ArticleModal';

class Article extends Component {
  constructor(props){
    super(props);
    this.state = {
      showArticle: false
    }

    this.openArticle = this.openArticle.bind(this);
    this.closeArticle = this.closeArticle.bind(this);
  }

  openArticle(){
    console.log('click');
  }

  closeArticle(){

  }


  render(){
    const {articleObj} = this.props;
    const {showArticle} = this.state;
    // console.log(articleObj);
    return(
      <div className="article_card">
        <span className='border'>
          <img className="img-thumbnail" 
              src={articleObj.urlToImage} alt="Card"
              onClick={this.openArticle}/>
        </span>
        <div className='article_text'>
          <a href='' className='article_link' onClick={this.openArticle}>
            <h6 className='article_title'>
              {articleObj.title}
            </h6>
          </a>
          <div className='article_content'>
            {articleObj.description}
            <button type='button' className='btn btn-link btn-sm'> Read More</button>
          </div>
        </div>
        {showArticle ? <ArticleModal onClose={this.closeArticle}/> : null}
      </div>
    )
  }
}

export default Article;


