import React from 'react';

const NewsApiBtn = () => {
  return (
    <div className='api_btn'>
      <a href="https://newsapi.org/" className="btn btn-md btn-primary" role="button">
        Data fetched from News Api
      </a>
    </div>
  )
}

export default NewsApiBtn;


